.App {
  text-align: center;
}

.lista{
  justify-content: space-around;

}

a {
  text-decoration:none;
}

ul {
  display: table;
  margin: 0 auto;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
  border-radius: 30px;
  margin-bottom: 40px;
}

.puntos {
  height: 30vmin;
  border-radius: 30px;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

p{
 width: 75%;
 font-size: 25px;
 text-align: center;
}

.lista-ordenada{
  display: flex;
  align-items: flex-start;
  height: fit-content;
  justify-content: flex-start;
}
li{
  text-align: left;
}
ul{
  padding-bottom: 50px;
}
h2{
  height: 100px;
}

h3{
  height: 20px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
